// pages/provider/dashboard.js
import { useEffect, useState } from "react";
import toast from "react-hot-toast";
import MyApplications from "../../components/MyApplications";

export default function ProviderDashboard() {
  const API_BASE = "https://khedme-api.onrender.com";
  const [loading, setLoading] = useState(false);
  const [jobs, setJobs] = useState([]);
  const [filteredJobs, setFilteredJobs] = useState([]);
  const [districtFilter, setDistrictFilter] = useState("All Districts");
  const [branchFilter, setBranchFilter] = useState("All Branches");
  const [activeTab, setActiveTab] = useState("jobs");
  const [provider, setProvider] = useState({
    id: 1, // placeholder until auth added
    name: "",
    branch: "",
    service: "",
    district: "",
  });

  /* ---------------------------------------------------
     ✅ Fetch All Jobs
  --------------------------------------------------- */
  const fetchJobs = async () => {
    try {
      const res = await fetch(`${API_BASE}/api/jobs/all`);
      const data = await res.json();
      if (data.jobs) {
        setJobs(data.jobs);
        setFilteredJobs(data.jobs);
      }
    } catch (err) {
      toast.error("Failed to load jobs");
      console.error(err);
    }
  };

  /* ---------------------------------------------------
     ✅ Apply for a Job
  --------------------------------------------------- */
  const applyForJob = async (jobId) => {
    setLoading(true);
    try {
      const res = await fetch(`${API_BASE}/api/providers/apply`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          job_id: jobId,
          provider_id: provider.id,
          message: "I’d like to apply for this job!",
        }),
      });

      const data = await res.json();
      if (res.ok) toast.success("Application sent ✅");
      else toast.error(data.message || "Failed to apply");
    } catch (err) {
      toast.error("Error sending application");
      console.error(err);
    } finally {
      setLoading(false);
    }
  };

  /* ---------------------------------------------------
     ✅ Filter Jobs by Branch or District
  --------------------------------------------------- */
  useEffect(() => {
    let filtered = [...jobs];
    if (branchFilter !== "All Branches") {
      filtered = filtered.filter((job) =>
        job.service.toLowerCase().includes(branchFilter.toLowerCase())
      );
    }
    if (districtFilter !== "All Districts") {
      filtered = filtered.filter(
        (job) => job.district === districtFilter
      );
    }
    setFilteredJobs(filtered);
  }, [branchFilter, districtFilter, jobs]);

  useEffect(() => {
    fetchJobs();
  }, []);

  /* ---------------------------------------------------
     🧱 UI
  --------------------------------------------------- */
  return (
    <div className="max-w-5xl mx-auto p-6">
      <h1 className="text-2xl font-bold text-orange-600 mb-6">
        Provider Dashboard
      </h1>

      {/* Tabs */}
      <div className="flex gap-3 mb-6">
        <button
          onClick={() => setActiveTab("jobs")}
          className={`px-4 py-2 rounded-lg font-medium transition ${
            activeTab === "jobs"
              ? "bg-orange-600 text-white"
              : "bg-gray-200 text-gray-700 hover:bg-gray-300"
          }`}
        >
          Available Jobs
        </button>
        <button
          onClick={() => setActiveTab("applications")}
          className={`px-4 py-2 rounded-lg font-medium transition ${
            activeTab === "applications"
              ? "bg-orange-600 text-white"
              : "bg-gray-200 text-gray-700 hover:bg-gray-300"
          }`}
        >
          My Applications
        </button>
      </div>

      {/* Jobs Tab */}
      {activeTab === "jobs" && (
        <>
          {/* Filters */}
          <div className="flex gap-4 mb-6">
            <select
              value={branchFilter}
              onChange={(e) => setBranchFilter(e.target.value)}
              className="border rounded-lg p-2"
            >
              <option>All Branches</option>
              <option>On-site</option>
              <option>Online</option>
            </select>

            <select
              value={districtFilter}
              onChange={(e) => setDistrictFilter(e.target.value)}
              className="border rounded-lg p-2"
            >
              <option>All Districts</option>
              <option>Beirut</option>
              <option>Tripoli</option>
              <option>Saida</option>
              <option>Tyre</option>
              <option>Zgharta</option>
              <option>Koura</option>
              <option>Baabda</option>
            </select>
          </div>

          {/* Job Cards */}
          {filteredJobs.length === 0 ? (
            <p className="text-gray-500">No jobs available.</p>
          ) : (
            <div className="grid gap-4">
              {filteredJobs.map((job) => (
                <div
                  key={job.id}
                  className="border rounded-xl p-4 shadow-sm bg-white flex flex-col md:flex-row md:items-center justify-between"
                >
                  <div>
                    <h2 className="text-lg font-semibold text-gray-800">
                      {job.service}
                    </h2>
                    <p className="text-gray-600 text-sm">{job.description}</p>
                    <div className="text-sm text-gray-500 mt-1">
                      {job.district} • ${job.budget}
                    </div>
                  </div>

                  <button
                    onClick={() => applyForJob(job.id)}
                    disabled={loading}
                    className="mt-3 md:mt-0 bg-orange-600 hover:bg-orange-700 text-white py-2 px-4 rounded-lg transition disabled:bg-gray-400"
                  >
                    {loading ? "Applying..." : "Apply"}
                  </button>
                </div>
              ))}
            </div>
          )}
        </>
      )}

      {/* Applications Tab */}
      {activeTab === "applications" && (
        <MyApplications providerId={provider.id} />
      )}
    </div>
  );
}
